"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Upload, Tag, Clock, FileText, Trash2, Pencil } from "lucide-react";
import { UploadDocumentModal } from "@/components/modals/upload-document-modal";

interface TutorDocument {
  id: string;
  title: string;
  subject: string;
  description?: string;
  price: number;
  fileName: string;
  uploadDate: string;
}

const mockTutorDocuments: TutorDocument[] = [
  {
    id: "doc1",
    title: "Calculus Cheat Sheet",
    subject: "Mathematics",
    description: "Quick reference guide for common calculus formulas and theorems.",
    price: 5.00,
    fileName: "calculus_cheat_sheet_v1.pdf",
    uploadDate: "2024-03-15",
  },
  {
    id: "doc2",
    title: "Physics Kinematics Explained",
    subject: "Physics",
    description: "In-depth explanation of kinematics concepts with examples.",
    price: 7.50,
    fileName: "physics_kinematics_notes.docx",
    uploadDate: "2024-03-18",
  },
  {
    id: "doc3",
    title: "Organic Chemistry Reaction Mechanisms",
    subject: "Chemistry",
    description: "Summary of key reaction mechanisms for Orgo 1.",
    price: 10.00,
    fileName: "orgo1_mechanisms.pdf",
    uploadDate: "2024-03-20",
  },
];

export default function TutorDocumentsPage() {
  const [documents, setDocuments] = useState<TutorDocument[]>(mockTutorDocuments);
  const [isUploadModalOpen, setIsUploadModalOpen] = useState(false);

  const handleUploadDocumentClick = () => {
    setIsUploadModalOpen(true);
  };

  const handleDocumentSubmit = (title: string, description: string, subject: string, price: number, file: File) => {
    const newDocument: TutorDocument = {
      id: crypto.randomUUID(),
      title,
      description,
      subject,
      price,
      fileName: file.name,
      uploadDate: new Date().toISOString().split('T')[0],
    };
    setDocuments(prevDocs => [newDocument, ...prevDocs]);
    console.log("New Document Submitted:", newDocument);
  };

  const handleDeleteClick = (docId: string) => {
    console.log("Delete document:", docId);
    setDocuments(prev => prev.filter(doc => doc.id !== docId));
    alert(`Simulation: Deleted document ${docId}`);
  };

  const handleEditClick = (doc: TutorDocument) => {
    console.log("Edit document:", doc.id);
    alert(`Simulation: Edit document ${doc.id}`);
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold">My Documents</h1>
          <p className="text-muted-foreground">
            Manage documents you've uploaded for students to purchase.
          </p>
        </div>
        <Button onClick={handleUploadDocumentClick}>
          <Upload className="mr-2 h-4 w-4" /> Upload Document
        </Button>
      </div>

      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
        {documents.length > 0 ? (
          documents.map((doc) => (
            <Card key={doc.id} className="flex flex-col h-full hover:shadow-md transition-shadow">
              <CardHeader className="pb-3">
                <CardTitle className="text-lg">{doc.title}</CardTitle>
                <CardDescription>{doc.subject}</CardDescription>
              </CardHeader>
              <CardContent className="flex-1 space-y-3 min-h-[60px]">
                {doc.description && (
                  <p className="text-sm text-muted-foreground line-clamp-3">{doc.description}</p>
                )}
                {!doc.description && <div className="flex-1"></div>}
              </CardContent>
              <div className="border-t px-6 py-3 text-sm flex items-center justify-between text-muted-foreground">
                <span className="flex items-center gap-1">
                  <Clock className="h-4 w-4" /> Uploaded: {doc.uploadDate}
                </span>
                <span className="flex items-center gap-1 font-semibold text-blue-600 dark:text-blue-400">
                  <Tag className="h-4 w-4" /> Price: €{doc.price.toFixed(2)}
                </span>
              </div>
              <CardFooter className="border-t px-6 py-3 flex items-center justify-end gap-2">
                <Button variant="ghost" size="icon" onClick={() => handleEditClick(doc)} title="Edit Document">
                  <Pencil className="h-4 w-4" />
                </Button>
                <Button variant="ghost" size="icon" onClick={() => handleDeleteClick(doc.id)} title="Delete Document">
                  <Trash2 className="h-4 w-4 text-destructive" />
                </Button>
              </CardFooter>
            </Card>
          ))
        ) : (
          <p className="text-center text-muted-foreground py-8 md:col-span-2 lg:col-span-3">
            You haven't uploaded any documents yet.
          </p>
        )}
      </div>

      <UploadDocumentModal
        isOpen={isUploadModalOpen}
        onClose={() => setIsUploadModalOpen(false)}
        onSubmit={handleDocumentSubmit}
      />
    </div>
  );
} 